"use client";

import { useState, useEffect } from "react";
import "maplibre-gl/dist/maplibre-gl.css";
import {
  getGlobalSettings,
  updateGlobalSettings,
} from "@/actions/adminActions";
import { generateInvite } from "@/actions/generateInvite";
import { useSession } from "@/app/lib/auth-client";
import MapSettings from "@/app/components/settings/MapSettings";
import AccessSettings from "@/app/components/settings/AccessSettings";
import UserSettings from "@/app/components/settings/UserSettings";
import { AdminSidebar } from "@/app/components/settings/AdminSidebar";
import { SidebarProvider } from "@/components/ui/sidebar";
import { Separator } from "@/components/ui/separator";

export default function AdminPanel() {
  const { data: session } = useSession();
  const [settings, setSettings] = useState({});
  const [loading, setLoading] = useState(true);
  const [updating, setUpdating] = useState(false);
  const [inviteLink, setInviteLink] = useState("");
  const [activeSection, setActiveSection] = useState("access");

  useEffect(() => {
    async function fetchSettings() {
      const data = await getGlobalSettings();
      setSettings(data);
      setLoading(false);
    }
    fetchSettings();
  }, []);

  const handleSave = async () => {
    setUpdating(true);
    await updateGlobalSettings(session, settings);
    setUpdating(false);
  };

  const handleGenerateInvite = async () => {
    const link = await generateInvite(session);
    setInviteLink(link);
  };

  if (!session || session.user.role !== "admin") {
    return <p>Unauthorized</p>;
  }

  if (loading) return <p>Loading settings...</p>;

  return (
    <SidebarProvider>
      <AdminSidebar
        activeSection={activeSection}
        setActiveSection={setActiveSection}
      />
      <div className="p-6 w-full">
        <main className="p-6">
          <h1 className="text-2xl font-bold">Admin Panel</h1>
          <Separator />
          <div className="mt-4 grid grid-cols-2 gap-4">
            {activeSection === "access" && (
              <AccessSettings
                settings={settings}
                setSettings={setSettings}
                handleSave={handleSave}
                updating={updating}
              />
            )}
            {activeSection === "map" && (
              <MapSettings
                settings={settings}
                setSettings={setSettings}
                handleSave={handleSave}
                updating={updating}
              />
            )}
            {activeSection === "users" && <UserSettings />}
            {/* 
              
          <div className="flex flex-col items-center mt-8">
            <Button onClick={handleGenerateInvite} className="mt-4">
              Generate Invite Link
            </Button>

            {inviteLink && (
              <p className="text-green-600 mt-2">
                Share this link:{" "}
                <a href={inviteLink} className="underline">
                  {inviteLink}
                </a>
              </p>
            )} */}
          </div>
        </main>
      </div>
    </SidebarProvider>
  );
}
