import { useState } from "react";
import { Map } from "react-map-gl/maplibre";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { toast } from "sonner";

export default function MapSettings({ settings, setSettings, handleSave }) {
  const [mapStyle, setMapStyle] = useState(""); // Fetch from settings initially

  //   const handleUpdate = async () => {
  //     await updateGlobalSetting({ mapStyle });
  //     toast.success("Map style updated!");
  //   };

  return (
    <div>
      <label className="font-semibold">Map Center Latitude</label>
      <Input
        type="number"
        value={settings.mapCenterLat}
        onChange={(e) =>
          setSettings({
            ...settings,
            mapCenterLat: parseFloat(e.target.value),
          })
        }
      />
      <div className="">
        <label className="font-semibold">Map Center Longitude</label>
        <Input
          type="number"
          value={settings.mapCenterLng}
          onChange={(e) =>
            setSettings({
              ...settings,
              mapCenterLng: parseFloat(e.target.value),
            })
          }
        />
      </div>
      <div className="">
        <Map
          style={{ width: "100%", height: "400px" }}
          mapStyle={`https://api.maptiler.com/maps/streets/style.json?key=${process.env.NEXT_PUBLIC_MAPTILER_KEY}`}
        />
      </div>
      <h2 className="text-lg font-bold mb-4">Map Settings</h2>
      <Input
        type="text"
        placeholder="Enter custom Map URL"
        value={mapStyle}
        onChange={(e) => setMapStyle(e.target.value)}
      />
      <Button onClick={handleSave} className="mt-2">
        Update
      </Button>
    </div>
  );
}
